
@keyframes splitEnter{
from{transform:scale(0.6);opacity:0
}
to{transform:scale(1);opacity:1
}
}
.l-split-enter-active{animation:splitEnter 700ms
}
.l-split-enter-active .left-column nav.menu-overlay{visibility:hidden
}
.l-split-enter-active .left-column .contents *{transform:scale(1) !important;opacity:1 !important
}
.l-split-leave{transform:scale(1);opacity:1
}
.l-split-leave-to{transform:scale(1);opacity:0
}
.l-split-leave-active{z-index:30;transition:opacity 200ms ease-out
}



