//  Color Modifiers
//  ---------------

.bg-standard {
  background-color: background-color(); }

.bg-primary {
  background-color: primary-color(); }

.bg-secondary {
  background-color: secondary-color(); }

.bg-accent {
  background-color: accent-color(); }

//  Grid Modifiers
//  --------------

.l-flex {
  display: flex; }

.full-width {
  width: 100vw; }

.full-height {
  height: 100vh; }

//  Typography Modifiers
//  --------------------

.type-decorative {
  font-family: $font-decorative; }

.t-bold {
  font-weight: 600; }

.t-light {
  font-weight: 300; }

.t-lighter {
  font-weight: 100; }



