
.section-title{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;width:100%;height:100%;transform:scale(1) !important
}
.wrapper-title{margin-top:25%;transform:scale(1) !important
}
@media (min-width: 1440px){
.wrapper-title{margin-top:33% !important
}
}
.wrapper-title .title{font-family:"Ailerons-Regular",sans-serif;font-size:4em;color:#FBF4DA;margin:0.67em 0 20px
}
.wrapper-title .project-links{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center
}
.wrapper-title .project-links .project-link{display:-ms-flexbox;display:flex;font-weight:400;font-size:0.7em;color:#FBF4DA;text-transform:uppercase
}
.wrapper-title .project-links .project-link::after{content:"";display:block;-ms-flex-item-align:center;-ms-grid-row-align:center;align-self:center;width:4px;height:4px;border-radius:100%;margin:3px 15px 0 15px;background-color:#E26E91
}
.wrapper-title .project-links .project-link:last-child::after{display:none
}



