
.wrapper-content{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:center;justify-content:center;position:absolute;width:100%;height:100%
}
.wrapper-content .content{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:center;justify-content:center;height:100%;margin:45px 0 30px 0
}
.wrapper-content .project-header{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;-ms-flex-negative:0;flex-shrink:0
}
.wrapper-content .project-header .project-avatar .avatar{font-size:8.642em;color:#FBF4DA
}
.wrapper-content .project-header .project-avatar .avatar::before{display:block;line-height:0.95
}
.wrapper-content .project-header .project-label .label{font-weight:100;font-size:4.9em;color:#FBF4DA;margin:2px 0 10px
}
.wrapper-content .project-header .project-platform-icon{font-size:.625em;padding:0 7px
}
.wrapper-content .wrapper-description{position:relative;overflow:hidden;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;margin:10px 50px
}
@media (min-width: 1440px){
.wrapper-content .wrapper-description{margin:10px 95px
}
}
.wrapper-content .wrapper-description .shadow-top,.wrapper-content .wrapper-description .shadow-bottom{position:absolute;z-index:50;left:1%;width:98%;height:17px;border-radius:48%;box-shadow:0px 0px 17px 0px transparent;transition:box-shadow 350ms ease-in-out
}
.wrapper-content .wrapper-description .shadow-top.shadow-top,.wrapper-content .wrapper-description .shadow-bottom.shadow-top{top:-17px
}
.wrapper-content .wrapper-description .shadow-top.shadow-bottom,.wrapper-content .wrapper-description .shadow-bottom.shadow-bottom{bottom:-17px
}
.wrapper-content .wrapper-description.show-shadow-top{border-top:1px solid rgba(251,244,218,0.04)
}
.wrapper-content .wrapper-description.show-shadow-top .shadow-top{box-shadow:0px 4px 17px 0px rgba(0,0,0,0.37)
}
.wrapper-content .wrapper-description.show-shadow-bottom{border-bottom:1px solid rgba(251,244,218,0.04)
}
.wrapper-content .wrapper-description.show-shadow-bottom .shadow-bottom{box-shadow:0px -4px 17px 0px rgba(0,0,0,0.3)
}
.wrapper-content .project-description{position:relative;-ms-flex-positive:1;flex-grow:1;-ms-flex-item-align:end;align-self:flex-end;overflow:scroll;padding:5px 10px
}
.wrapper-content .project-description.shadow-top{box-shadow:0px 19px 23px -25px rgba(0,0,0,0.35) inset;border-top:1px solid #fafafa08
}
.wrapper-content .project-description.shadow-bottom{box-shadow:0px -19px 23px -25px rgba(0,0,0,0.35) inset;border-bottom:1px solid #fafafa08
}
.wrapper-content .project-description p{font-size:.92em;font-weight:300;line-height:1.4;text-align:center;color:#FBF4DA
}
.wrapper-content .project-description p:first-child{margin-top:0
}
.wrapper-content .project-controls{display:-ms-flexbox;display:flex;-ms-flex-negative:0;flex-shrink:0;-ms-flex-pack:center;justify-content:center
}
.wrapper-content .project-controls .project-control{margin:0 15px
}
.wrapper-content .project-controls .project-control .floating-button{width:20.625rem
}



