
.jump-enter *{transform:scale(0.5);opacity:0.5
}
.jump-enter-to *{transform:scale(1);opacity:1
}
.jump-enter-active{transition:transform 500ms ease-in, opacity 650ms ease-in
}
.jump-enter-active *{transition:transform 500ms ease-in, opacity 650ms ease-in !important
}
.jump-leave *{transform:scale(1, 1);opacity:1
}
.jump-leave-to *{transform:scale(0.5);opacity:0.5
}
.jump-leave-active{transition:transform 500ms ease-in, opacity 650ms ease-in
}
.jump-leave-active *{transition:transform 500ms ease-in, opacity 650ms ease-in !important
}



