from{transform:translate3d(100%, 0, 0);visibility:visible
}
to{transform:translate3d(0, 0, 0)
}
}
.slideInRight{animation-name:slideInRight
}
@keyframes slideInUp{
from{transform:translate3d(0, 100%, 0);visibility:visible
}
to{transform:translate3d(0, 0, 0)
}
}
.slideInUp{animation-name:slideInUp
}
@keyframes slideOutDown{
from{transform:translate3d(0, 0, 0)
}
to{visibility:hidden;transform:translate3d(0, 100%, 0)
}
}
.slideOutDown{animation-name:slideOutDown
}
@keyframes slideOutLeft{
from{transform:translate3d(0, 0, 0)
}
to{visibility:hidden;transform:translate3d(-100%, 0, 0)
}
}
.slideOutLeft{animation-name:slideOutLeft
}
@keyframes slideOutRight{
from{transform:translate3d(0, 0, 0)
}
to{visibility:hidden;transform:translate3d(100%, 0, 0)
}
}
.slideOutRight{animation-name:slideOutRight
}
@keyframes slideOutUp{
from{transform:translate3d(0, 0, 0)
}
to{visibility:hidden;transform:translate3d(0, -100%, 0)
}
}
.slideOutUp{animation-name:slideOutUp
}
.slide-left-enter{opacity:0.1
}
.slide-left-enter-to{opacity:1
}
.slide-left-enter-active{position:absolute;z-index:5;background-color:transparent !important;transition:opacity 500ms ease-in;animation-name:slideInRight;animation-duration:500ms;animation-fill-mode:both
}
.slide-left-leave{opacity:1
}
.slide-left-leave-to{opacity:0
}
.slide-left-leave-active{position:absolute;z-index:5;background-color:transparent !important;transition:opacity 300ms;animation-name:slideOutLeft;animation-duration:500ms;animation-fill-mode:both
}



