
.left-column{position:relative;display:-ms-flexbox;display:flex;z-index:10;max-height:100vh;background-color:#394B57
}
.left-column::before{content:"";display:block;position:absolute;right:0px;z-index:25;width:0px;height:calc(100% - 100px);margin:50px 0;border-right-width:1px;border-right-color:rgba(251,244,218,0.3);border-right-style:solid
}
.left-column.fade-out .contents *{transform:scale(0.5);opacity:0.3
}
.left-column.fade-out .fa,.left-column.fade-out .icn,.left-column.fade-out [data-icon],.left-column.fade-out [class^="icn-"],.left-column.fade-out [class*=" icn-"]{transform:scale(1) !important
}
.left-column .contents{z-index:10
}
.left-column .contents *{transition:transform 500ms, opacity 400ms
}
.right-column{position:relative;max-height:100vh;overflow-x:hidden;z-index:5;background-color:#394B57
}



