
.wrapper-hero{position:absolute;top:15%
}
@media (min-width: 1440px){
.wrapper-hero{top:24%
}
}
.site-header .site-avatar{font-size:7rem;color:#E26E91
}
.site-header .site-caption{font-family:"Lato",sans-serif;font-weight:100;font-size:5rem;text-align:center;color:#E26E91;margin:0
}
.contact-links{margin:20px 0 0
}
.contact-link{list-style-type:none;margin:0 1.375rem
}
.contact-link .icon-link .fa.icn-github,.contact-link .icon-link .icn.icn-github,.contact-link .icon-link [data-icon].icn-github,.contact-link .icon-link [class^="icn-"].icn-github,.contact-link .icon-link [class*=" icn-"].icn-github{font-size:3.3rem;-ms-flex-item-align:end;align-self:flex-end;margin-bottom:-5px
}



