.container {
  @include flex-grid-row(collapse, none);
  min-height: 100vh;

  &.inner {
    @include flex-grid-column; }

  &.collapse-self {
    padding-right: 0;
    padding-left: 0; }

  > .container {
    @include grid-col-collapse; }

  &.collapsed {
    > .column, .columns {
      @include grid-col-collapse; }

    > .column > .row, .container > .row {
      margin-left: 0;
      margin-right: 0; } } }



