
.c-hamburger{display:block;position:relative;overflow:hidden;z-index:35;width:34px;height:34px;font-size:0;text-indent:-9999px;margin:0;padding:0;-webkit-appearance:none;-moz-appearance:none;appearance:none;box-shadow:none;border-radius:none;border:none;cursor:pointer;transition:background .3s
}
.c-hamburger .c-hamburger-bars{display:block;position:absolute;top:15px;left:4px;right:4px;height:4px;background-color:#E26E91
}
.c-hamburger .c-hamburger-bars::after,.c-hamburger .c-hamburger-bars::before{content:"";display:block;position:absolute;left:0;width:100%;height:4px;background-color:#E26E91
}
.c-hamburger .c-hamburger-bars::after{bottom:-9px
}
.c-hamburger .c-hamburger-bars::before{top:-9px
}
.colors-background .c-hamburger .c-hamburger-bars{background-color:#394B57
}
.colors-background .c-hamburger .c-hamburger-bars::after,.colors-background .c-hamburger .c-hamburger-bars::before{background-color:#394B57
}
.c-hamburger.colors-background .c-hamburger-bars{background-color:#394B57
}
.c-hamburger.colors-background .c-hamburger-bars::after,.c-hamburger.colors-background .c-hamburger-bars::before{background-color:#394B57
}
.colors-foreground .c-hamburger .c-hamburger-bars{background-color:#FBF4DA
}
.colors-foreground .c-hamburger .c-hamburger-bars::after,.colors-foreground .c-hamburger .c-hamburger-bars::before{background-color:#FBF4DA
}
.c-hamburger.colors-foreground .c-hamburger-bars{background-color:#FBF4DA
}
.c-hamburger.colors-foreground .c-hamburger-bars::after,.c-hamburger.colors-foreground .c-hamburger-bars::before{background-color:#FBF4DA
}
.colors-accent .c-hamburger .c-hamburger-bars{background-color:#E26E91
}
.colors-accent .c-hamburger .c-hamburger-bars::after,.colors-accent .c-hamburger .c-hamburger-bars::before{background-color:#E26E91
}
.c-hamburger.colors-accent .c-hamburger-bars{background-color:#E26E91
}
.c-hamburger.colors-accent .c-hamburger-bars::after,.c-hamburger.colors-accent .c-hamburger-bars::before{background-color:#E26E91
}
.colors-secondary .c-hamburger .c-hamburger-bars{background-color:#47B7A9
}
.colors-secondary .c-hamburger .c-hamburger-bars::after,.colors-secondary .c-hamburger .c-hamburger-bars::before{background-color:#47B7A9
}
.c-hamburger.colors-secondary .c-hamburger-bars{background-color:#47B7A9
}
.c-hamburger.colors-secondary .c-hamburger-bars::after,.c-hamburger.colors-secondary .c-hamburger-bars::before{background-color:#47B7A9
}
.colors-primary .c-hamburger .c-hamburger-bars{background-color:#394B57
}
.colors-primary .c-hamburger .c-hamburger-bars::after,.colors-primary .c-hamburger .c-hamburger-bars::before{background-color:#394B57
}
.c-hamburger.colors-primary .c-hamburger-bars{background-color:#394B57
}
.c-hamburger.colors-primary .c-hamburger-bars::after,.c-hamburger.colors-primary .c-hamburger-bars::before{background-color:#394B57
}
.c-hamburger--htx{background-color:transparent
}
.c-hamburger--htx .c-hamburger-bars{transition:background-color 0s ease .3s
}
.c-hamburger--htx .c-hamburger-bars::after,.c-hamburger--htx .c-hamburger-bars::before{transition-duration:.3s,.3s;transition-delay:.3s,0s
}
.c-hamburger--htx .c-hamburger-bars::after{transition-property:bottom, transform
}
.c-hamburger--htx .c-hamburger-bars::before{transition-property:top, transform
}
.c-hamburger--htx.is-active .c-hamburger-bars{background:none
}
.c-hamburger--htx.is-active .c-hamburger-bars::after{bottom:0px;transform:rotate(-45deg)
}
.c-hamburger--htx.is-active .c-hamburger-bars::before{top:0px;transform:rotate(45deg)
}
.c-hamburger--htx.is-active .c-hamburger-bars::after,.c-hamburger--htx.is-active .c-hamburger-bars::before{transition-delay:0s,.3s,0s
}



